import React, { Component } from 'react'
import PropTypes from 'prop-types'
import Page from './Page'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faEnvelope, faEnvelopeOpen, faSun, faUserFriends } from '@fortawesome/free-solid-svg-icons'
import { Link } from 'react-router-dom'

export class PageContactUs extends Component {

    render() {
        return (
            <Page title="  ">
                <p>              . </p>
                <div className="field">
                    <label className="label"></label>
                    <div className="control has-icons-right">
                    <input className="input" type="text" placeholder=" " />
                    <span className="icon is-small is-right">
                        <FontAwesomeIcon icon={faUserFriends} />
                    </span>
                    </div>
                </div>
                <div className="field">
                    <label className="label"></label>
                    <div className="control  has-icons-right">
                    <input className="input" type="email" placeholder="     "  />
                    <span className="icon is-small is-right">
                        <FontAwesomeIcon icon={faEnvelopeOpen} />
                    </span>
                    </div>
                </div>
                <div className="field">
                    <label className="label"></label>
                    <div className="control  has-icons-right">
                        <input className="input" type="text" placeholder="     ... ." />
                        <span className="icon is-small is-right">
                        <FontAwesomeIcon icon={faSun} />
                        </span>
                    </div>
                </div>
                <div className="field">
                    <label className="label"></label>
                    <div className="control">
                    <textarea className="textarea" placeholder="Textarea" defaultValue={""} />
                    </div>
                </div>
                <div className="field">
                    <div className="control">
                    <label className="checkbox">
                        <input type="checkbox" className="m-1"/>
                          <Link to="/policy">  </Link>   .
                    </label>
                    </div>
                </div>
                <div className="field is-grouped">
                    <div className="control">
                    <button className="button is-link"></button>
                    </div>
                    <div className="control">
                    <button className="button is-link is-light"></button>
                    </div>
                </div>
            </Page>
        )
    }
}

export default PageContactUs
